  -webkit-transition: all ease 0.5s;
  transition: all ease 0.5s;
  opacity: 0;
  top: 0;
  left: 2.9em;
  width: auto;
  background-color: #b5c7d5;
  border-radius: 0.2em;
}
.search-area > .tags > .list > .tag {
  display: table;
